@keyframes gradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}


/* width */
body::-webkit-scrollbar {
  width: 6px;
}

/* Track */
body::-webkit-scrollbar-track {
  box-shadow: inset 0 0 1px #322d38; 
  background: transparent;
}
 
/* Handle */
body::-webkit-scrollbar-thumb {
    background: linear-gradient(180deg, #503361, #403a47);
  border-radius: 10px;
}

/* Handle on hover */
body::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(180deg,#503361, #4e425c);
}