@import './../../../scss/global.scss';

.wrapper {
  width: 220px;
  height: 220px;
  border: 1px solid $color-text;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  padding: 38px;
  @include devices(mobile) {
    width: 120px;
    height: 120px;
    padding: 24px;
  }
  img {
    width: 95%;
    height: 95%;
  }
}
